$space-between: 5px;

@mixin buttonstyle() {
  outline: none;
  border: none;
  background-color: rgb(62, 87, 63);
  color: #fff;
  font: {size: 1.2rem;}
}

.create-sprite__frames {
  height: 768px;
  overflow: auto;
  text-align: center;

  .create-sprite__frames__frames-item {
    margin: $space-between;
    margin-bottom: $space-between * 2;
    border: 3px solid rgb(62, 87, 63);
    background-image: url(../../screens/app_screen//assets/square.png);
    width: 128px;
    height: 128px;
    position: relative;

    canvas {
      position: absolute;
      top: 0;
      left: 0;
      transform-origin: 0 0;
      image-rendering: pixelated;
      image-rendering: crisp-edges;
    }

    button {
      @include buttonstyle();
      position: absolute;
      bottom: $space-between;
    }

    .frames__frames-item__copy {
      left: $space-between;
    }

    .frames__frames-item__delete {
      right: $space-between;
    }
  }

  .create-sprite__frames__add {
    @include buttonstyle();
    width: 50%;
    margin-bottom: $space-between * 2;
  }

  .frame__current {
    border: 3px solid rgb(255, 156, 62);
  }
}
