  $primary-color: rgb(21, 26, 17);
  $space-between: 5px;

@mixin flexible() {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@mixin buttonstyle-orange() {
  padding: $space-between;
  background-color: rgb(255, 156, 62);
  border-radius: 5%;
  color: $primary-color;
}

@mixin buttonstyle-green() {
  outline: none;
  border: none;
  width: 40px;
  height: 40px;
  background-color: rgb(62, 87, 63);
  color: #fff;
  font: {size: 1.2rem;}
}

body {
  margin: 0;
  padding: 0;
  background-color: rgb(46, 49, 42);
}

a {
  text-decoration: none;
  color: $primary-color;
}

button:hover {
  cursor: pointer;
}

header {
  height: 20px;
  background: rgb(147, 160, 134);
  color: $primary-color;
  font: {weight: bold;}
  padding: $space-between * 2;
  margin-bottom: $space-between;
  @include flexible();

  .header__logo {
    color: rgb(255, 123, 0);
    font: {size: 2.0rem; weight: bold;}
    text-shadow: rgb(46, 49, 42) 1px 1px 0, rgb(46, 49, 42) -1px -1px 0,
                 rgb(46, 49, 42) -1px 1px 0, rgb(46, 49, 42) 1px -1px 0;
  }

  .header__links {
    @include flexible();

    .header__links__create {
      @include buttonstyle-orange();
    }

    .save-storage {
      font: inherit;
      outline: none;
      border: none;
      @include buttonstyle-orange();
      margin-right: $space-between;
    }
  }
}

.create-sprite {
  color: $primary-color;
  @include flexible();

  .create-sprite__tools {
    margin-left: $space-between;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: $space-between;

    button {
      @include buttonstyle-green();
    }

    .button__choose {
      border: 2px solid rgb(255, 156, 62);
    }
  }

  .create-sprite__colors {
    @include flexible();
    flex-direction: column;

    input[type="color"] {
      -webkit-appearance: none;
      border: none;
      margin: $space-between;
      width: 50px;
      height: 50px;
    }

    .create-sprite__colors__first-color::-webkit-color-swatch-wrapper {
      border: 2px solid rgb(255, 156, 62);
    }

    input[type="color"]::-webkit-color-swatch-wrapper {
      padding: 0;
    }

    button {
      @include buttonstyle-green();
      width: 20px;
      height: 20px;
      font: {size: 0.9rem;}
    }
  }

  .create-sprite__area {
    min-height: 900px;
    background-color: rgb(190, 197, 180);
    flex-grow: 0.7;
    padding: $space-between;
    overflow: hidden;
    position: relative;

    .create-sprite__area__details {
      position: absolute;
      bottom: 0;
      right: 0;
      color: rgb(168, 84, 6);
      font: {weight: bold; size: 1.2rem;}
      text-align: center;
    }

    .create-sprite__area__canvas-container {
      width: 768px;
      height: 768px;
      background-image: url(./assets/square.png);
      margin: 50px auto;
      position: relative;

      .create-sprite__area__sub-canvas {
        position: absolute;
        transform-origin: 0 0;
        z-index: 1;
        transform: scale(24, 24);
        image-rendering: pixelated;
        image-rendering: crisp-edges;
      }

      .create-sprite__area__main-canvas {
        @extend .create-sprite__area__sub-canvas;
        z-index: 2;
      }
    }
  }

  .create-sprite__animation {
    background-color: rgb(190, 197, 180);
    align-self: flex-start;
  }

  .create-sprite__animation {
    position: relative;
    width: 192px;
    height: 192px;
    background-image: url(./assets/square.png);

    .create-sprite__animation__preview-canvas {
      position: absolute;
      top: 0;
      left: 0;
      transform-origin: 0 0;
      transform: scale(6, 6);
      image-rendering: pixelated;
      image-rendering: crisp-edges;
    }

    .create-sprite__animation__full-screen {
      @include buttonstyle-green();
      position: absolute;
      top: 0;
      right: 0;
    }

    input[type="range"] {
      position: absolute;
      bottom: -30px;
      left: 15%;
    }
  }

  .create-sprite__app-settings {
    @include flexible();
    flex-direction: column;

    label {
      color: #fff;
      margin-bottom: $space-between;
    }

    button {
      @include buttonstyle-green();
      margin: $space-between * 2 0;
      width: 80px;
      height: 80px;
      font: {size: 2rem;}

      span {
        font: {size: 1.2rem;}
      }
    }
  }
}

.invisible {
  visibility: hidden;
}


